import React from "react";
import "./Header.css";
import NavItem from "./NavItem";
import Home from "@material-ui/icons/Home";
import Search from "@material-ui/icons/Search";
import Series from "@material-ui/icons/MovieCreationRounded";

import WishList from "@material-ui/icons/AddRounded";
import Original from "@material-ui/icons/StarRounded";
import Movies from "@material-ui/icons/GroupWorkRounded";

import { Avatar } from "@material-ui/core";

function Header() {
  return (
    <nav className="Header">
      <img
        className="Header_RowLogo_1"
        src="https://www.androidcentral.com/sites/androidcentral.com/files/field/image/2019/03/disneyplus-logo-fmi.png"
      />
      <div className="Header_RowNav_2">
        <NavItem Icon={<Home />} Primary="Home" linkto="/"/>
        <NavItem Icon={<Search />} Primary="Search" linkto="/login" />
        <NavItem Icon={<WishList />} Primary="WishList" />
        <NavItem Icon={<Original />} Primary="originals" linkto="/detail" />
        <NavItem Icon={<Movies />} Primary="Movies" />
        <NavItem Icon={<Series />} Primary="series" />
      </div>
      <div className="Header_RowAvatar_3">
        <Avatar src="data:image/jpeg;base64," />
      </div>
      <div className="Header_RowMenu_4">
          <span className="menuLine line-1"></span>
          <span className="menuLine line-2"></span>

      </div>
    </nav>
  );
}

export default Header;
